
.main-toolbar {
  z-index: 1000;
  width: 100%;
  position: fixed;
  transition: background 0.4s;
  background: transparent;
  color: #FBFBFF;
}
.main-toolbar.solid {
    background: #20BF55;
    box-shadow: 1px 1px 7px #000000 !important;
}
.main-toolbar .fixed-style {
    color: #161616;
}
.main-toolbar.toolbar--light .toolbar__item:hover {
    background-color: rgba(255, 255, 255, 0.1);
}
.main-toolbar__title .toolbar-text, .main-toolbar__item .toolbar-text {
    color: #FBFBFF;
    font-weight: 400;
}
.main-toolbar__title .fixed-style, .main-toolbar__item .fixed-style {
    color: #161616;
}



