
.header {
  background: radial-gradient(ellipse at 50% 60%, rgba(255, 255, 255, 0), black), url("../assets/moreSky.jpg");
  background-size: cover;
  background-attachment: fixed;
  position: absolute;
  left: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row nowrap;
          flex-flow: row nowrap;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  height: 100vh;
  width: 100%;
  padding: 40px;
}
.header #clouder {
    height: 300px;
    width: 300px;
}
.header__banner {
    width: 60%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-flow: column;
            flex-flow: column;
}
.header__main-banner {
    text-shadow: 2px 3px 10px black;
    -webkit-transform: translateY(-15%);
            transform: translateY(-15%);
    font-size: 64px;
    font-weight: 900;
    z-index: 99;
    color: #FFF;
    top: 50%;
    width: 60%;
}
.header__main-banner.name {
      -ms-flex-item-align: start;
          align-self: flex-start;
      width: auto;
}
.header__main-banner.great {
      -ms-flex-item-align: start;
          align-self: flex-start;
      font-size: 72px;
      width: auto;
      color: #20BF55;
}
.header__main-banner.developer {
      -ms-flex-item-align: start;
          align-self: flex-start;
      width: auto;
}
.header .fade-enter-active, .header .fade-leave-active {
    transition: opacity .3s;
}
.header .fade-enter, .header .fade-leave-to {
    opacity: 0;
}
.header canvas.particles-js-canvas-el {
    position: absolute;
    top: 0;
    left: 0;
}



